import Vue from 'vue'
import Router from "vue-router"
Vue.use(Router)

const constRoutes = [
  {
    path: "/",
    component: () => import('@/Views/index')
  },
  {
    path: "/pool",
    component: () => import('@/Views/pool')
  },
  {
    path: "*",
    component: () => import('@/Views/index')
  }
]

export default new Router({
  mode: "hash",
  routes: constRoutes
});
