<template>
  <div class="nav-container">
      <header>
          <div class="left">
              <img src="../assets/img/logo.png" alt="">
              <span class="text">BTswap</span>
          </div>
          <div class="right">
              <a class="text" href="http://btswap.com/whitepaper.pdf" target="_blank">Whitepaper</a>
              <a class="text" href="https://github.com/btswap" target="_blank">Github</a>
              <a class="text" href="#guide">Guidelines</a>
              <a class="text" href="javascript:;" @click="goToPool">{{poolRouter}}</a>
              <a href="https://ex.btswap.com/" target="_blank" class="btn">Trade Now</a>
          </div>
      </header>
  </div>
</template>

<script>
export default {
  name: 'Nav',
  data(){
      return {
          poolRouter:'Mining Pool'
      }
  },
  methods:{
      goToPool(){
          if(this.$router.history.current.path.indexOf('pool')>-1){
              this.$router.push({ path:'/'});
              this.poolRouter = 'Mining Pool'
          }else{
              this.$router.push({ path:'/pool'});
              this.poolRouter = 'Home'
          }
      }
  }
}
</script>

